import React from 'react';

class ContentCart extends React.Component {


    render() {
        return (
            <div className={this.props.class.container}>
                <p className={this.props.class.title}>{this.props.title}</p>
                <p className={this.props.class.content}>{this.props.content}</p>
            </div>
        );
    }
}

export default ContentCart;