import React from 'react';
import InputLogin from "../views/components/Authenticated/InputLogin";
import InputPassword from "../views/components/Authenticated/InputPassword";
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {changeLogin, changePassword, signIn} from "../actions/authorizationActions";
import NavBar from "../views/components/NavBar/NavBar";
import Header from "../views/components/Header/Header";
import Footer from "../views/components/Footer/Footer";

class Authorization extends React.Component {
    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.signIn({
            login: this.props.login,
            password: this.props.password
        });

    }

    render() {
        const {setLogin, setPassword, login, password, isFetching, message} = this.props;
        return (
            <div>
                <Header/>
                <NavBar/>
                <div className="authorization-body">
                    <div className="authorization__title">
                        <h3>Login Page</h3>
                    </div>
                    <div className="authorization__content">
                        <form className={'authorization__content--form'} onSubmit={this.handleSubmit}>

                            <InputLogin login={login} setValue={setLogin} class={'Login'}/>
                            <InputPassword password={password} class={'Password'} setValue={setPassword}/>
                            <button type="submit">sign in</button>
                        </form>
                        <div className="authorization__content--status">
                            <p>Login: {login}</p>
                            <p>Password: {password}</p>
                            {
                                (isFetching) ? <p>Отправка на Node.js...</p> :
                                    <p>{message}</p>
                            }
                        </div>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}


Authorization.propsTypes = {
    isFetching: PropTypes.bool.isRequired,
    message: PropTypes.string,
    login: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    setLogin: PropTypes.func.isRequired,
    setPassword: PropTypes.func.isRequired
};

const mapStateToProps = store => {
    console.log(store);
    return {
        login: store.authorization.login,
        password: store.authorization.password,
        isFetching: store.authorization.isFetching,
        message: store.authorization.message
    }
};
const mapDispatchToProps = dispatch => {
    return {
        setLogin: login => dispatch(changeLogin(login)),
        setPassword: password => dispatch(changePassword(password)),
        signIn: data => dispatch(signIn(data))
    }
};
export default connect(mapStateToProps, mapDispatchToProps)(Authorization);
