import React from 'react';
import {connect} from 'react-redux';
import NavBar from "../views/components/NavBar/NavBar";
import ContentCart from "../views/components/Home/ContentCart";
import Footer from "../views/components/Footer/Footer";
import Header from "../views/components/Header/Header";


class Home extends React.Component {
    render() {
        const {store} = this.props;
        let classNames = {
            container: "home__content--container",
            title: "home__content--title",
            content: "home__content--content"
        };
        return (
            <div>
                <Header/>
                <NavBar/>
                <div className={'home-body'}>
                    <div className={'home__title'}>
                        <h3>Home page</h3>
                    </div>
                    <div className={'home__content'}>
                        {
                            (store.authorization.token !== null) ?
                                Object.keys(store.authorization).map(e => {
                                    if (e !== 'token' && store.authorization[e]!=='')
                                        return <ContentCart
                                            key={e}
                                            class={classNames}
                                            title={e + ':'}
                                            content={store.authorization[e]}
                                        />;
                                })
                                :
                                <ContentCart
                                    class={classNames}
                                    title={'Ошибка:'}
                                    content={'Вы не авторизованы'}
                                />
                        }
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

const mapStateToProps = store => {
    return {
        store: store
    }
};
export default connect(mapStateToProps)(Home);