import {
    CHANGE_USER,
    DELETE_USER_FAILED,
    DELETE_USER_REQUEST,
    DELETE_USER_SUCCESS,
    GET_USERS_FAILED,
    GET_USERS_REQUEST,
    GET_USERS_SUCCESS,
    INSERT_USER_FAILED,
    INSERT_USER_REQUEST,
    INSERT_USER_SUCCESS,
    UPDATE_USER_FAILED,
    UPDATE_USER_REQUEST,
    UPDATE_USER_SUCCESS
} from "../constants/users";

const initialState = {
    selectedId: null,
    users: [],
    isFetching: false,
    message: ""
};

export function usersReducer(state = initialState, action) {
    switch (action.type) {
        case CHANGE_USER:
            return Object.assign({}, state, {selectedId: action.payload});
        case GET_USERS_REQUEST:
            return Object.assign({}, state, {isFetching: true, message: "Запрос на получение данных"});
        case INSERT_USER_REQUEST:
            return Object.assign({}, state, {isFetching: true, message: "Запрос на добавление"});
        case UPDATE_USER_REQUEST:
            return Object.assign({}, state, {isFetching: true, message: "Запрос на изменение"});
        case DELETE_USER_REQUEST:
            return Object.assign({}, state, {isFetching: true, message: "Запрос на удаление"});
        case GET_USERS_SUCCESS:
            return Object.assign({}, state, {users: action.payload});
        case INSERT_USER_SUCCESS:
            return Object.assign({}, state, {
                users: action.payload,
                isFetching: true,
                message: "Успешное добавление"
            });
        case UPDATE_USER_SUCCESS:
            return Object.assign({}, state, {
                users: action.payload,
                isFetching: true,
                message: "Успешное изменение"
            });
        case DELETE_USER_SUCCESS:
            return Object.assign({}, state, {
                users: action.payload,
                isFetching: true,
                message: "Успешное удаление"
            });
        case INSERT_USER_FAILED:
        case UPDATE_USER_FAILED:
        case DELETE_USER_FAILED:
        case GET_USERS_FAILED:
            return Object.assign({}, state, {
                isFetching: true,
                message: action.message
            });
        default:
            return state;
    }
}