import {
    CHANGE_LOGIN,
    CHANGE_PASSWORD,
    SIGN_IN_FAILED,
    SIGN_IN_REQUEST,
    SIGN_IN_SUCCESS,
    SIGN_OUT, TEST
} from "../constants/authorization";
import {
    AUTH_WITH_TOKEN_REQUEST,
    AUTH_WITH_TOKEN_SUCCESS,
    AUTH_WITH_TOKEN_FAILED,
    REFRESH_TOKEN_REQUEST,
    REFRESH_TOKEN_SUCCESS,
    REFRESH_TOKEN_FAILED,
    RESTORE_TOKEN, REFRESH_TOKEN_NO_NEED
} from "../constants/token";
import axios from "axios";

export function changeLogin(login) {
    return {
        type: CHANGE_LOGIN,
        payload: login
    }
}

export function changePassword(password) {
    return {
        type: CHANGE_PASSWORD,
        payload: password
    }
}

export function signIn(data) {
    return dispatch => {
        dispatch({
            type: SIGN_IN_REQUEST
        });
        axios.post('/request/login/login-auth', {
                login: data.login,
                password: data.password
            }
        )
            .then(response => {
                if (response.status !== 400) {
                    localStorage.setItem('accessToken', response.data.accessToken);
                    localStorage.setItem('refreshToken', response.data.refreshToken);
                    dispatch({
                        type: SIGN_IN_SUCCESS,
                        payload: {
                            message: "Welcome, " + response.data.login,
                            accessToken: response.data.accessToken,
                            refreshToken: response.data.refreshToken,
                            accessExpiresIn: response.data.accessExpiresIn
                        }
                    });
                } else
                    throw new Error();
            })
            .catch(reason => dispatch({
                type: SIGN_IN_FAILED,
                payload: {message: "Error: uncorrected login or password"}
            }));
    }
}

export function signOut() {
    localStorage.removeItem('accessToken');
    localStorage.removeItem('refreshToken');
    return {
        type: SIGN_OUT,
        login: "",
        password: "",
        accessToken: null,
        refreshToken: null,
        accessExpiresIn: 0
    }
}

export function signInWithToken(accessToken) {
    return dispatch => {
        dispatch({
            type: AUTH_WITH_TOKEN_REQUEST
        });
        axios.post('/request/login/token-auth', {}, {headers: {'authorization': 'Bearer ' + accessToken}})
            .then(response => {
                if (response.status === 200) {
                    dispatch({
                        type: AUTH_WITH_TOKEN_SUCCESS,
                        payload: Object.assign({message: `Welcome, ${response.data.user.login}`}, response.data.user)
                    });
                } else {
                    throw new Error();
                }
            })
            .catch(reason => {
                localStorage.removeItem('accessToken');
                dispatch({
                    type: AUTH_WITH_TOKEN_FAILED,
                    payload: {
                        accessToken: null,
                        accessExpiresIn: 0,
                        message: 'Невалидный или отсутствующий токен'
                    }
                })
            })
    }
}

export function refreshAccessToken(refreshToken, accessExpiresIn) {
    return dispatch => {
        dispatch({
            type: REFRESH_TOKEN_REQUEST
        });
        let nowTime = Math.floor(new Date().getTime() / 1000);
        if ((nowTime - accessExpiresIn) <= 0)
            axios.post('/request/login/refresh-token', {},
                {headers: {'refresh': 'Bearer ' + refreshToken}})
                .then(response => {
                    if (response.status === 200) {
                        localStorage.setItem('accessToken', response.data.accessToken);
                        localStorage.setItem('refreshToken', response.data.refreshToken);
                        dispatch({
                            type: REFRESH_TOKEN_SUCCESS,
                            payload: response.data
                        })
                    } else {
                        throw  new Error();
                    }
                })
                .catch(reason => {
                    localStorage.removeItem('accessToken');
                    localStorage.removeItem('refreshToken');
                    dispatch({
                        type: REFRESH_TOKEN_FAILED
                    });
                });
        else
            dispatch({
                type: REFRESH_TOKEN_NO_NEED
            })
    }
}


export function restoreToken() {
    let accessToken = localStorage.getItem('accessToken');
    let refreshToken = localStorage.getItem('refreshToken');
    return {
        type: RESTORE_TOKEN,
        payload: {
            accessToken: (accessToken !== null ? accessToken : null),
            refreshToken: (refreshToken !== null ? refreshToken : null)
        }
    }
}