import {
    CHANGE_LOGIN,
    CHANGE_PASSWORD,
    SIGN_IN_FAILED,
    SIGN_IN_REQUEST,
    SIGN_IN_SUCCESS,
    SIGN_OUT,
} from "../constants/authorization";
import {
    AUTH_WITH_TOKEN_FAILED,
    AUTH_WITH_TOKEN_REQUEST,
    AUTH_WITH_TOKEN_SUCCESS,
    REFRESH_TOKEN_FAILED, REFRESH_TOKEN_NO_NEED,
    REFRESH_TOKEN_REQUEST,
    REFRESH_TOKEN_SUCCESS, RESTORE_TOKEN
} from "../constants/token";

const initialState = {
    id: null,
    login: "",
    password: "",
    isFetching: false,
    message: "",
    accessToken: null,
    refreshToken: null,
    accessExpiresIn: 0
};

export function authorizationReducer(state = initialState, action) {
    switch (action.type) {
        case CHANGE_LOGIN:
            return Object.assign({}, state, {login: action.payload});
        case CHANGE_PASSWORD:
            return Object.assign({}, state, {password: action.payload});
        case SIGN_IN_REQUEST:
            return Object.assign({}, state, {isFetching: true});
        case SIGN_IN_SUCCESS:
            return Object.assign({}, state, action.payload, {password: "", isFetching: false});
        case SIGN_IN_FAILED:
            return Object.assign({}, state, action.payload, {isFetching: false});
        case SIGN_OUT:
            return Object.assign({}, initialState);
        case RESTORE_TOKEN:
            return Object.assign({}, state, action.payload);
        case AUTH_WITH_TOKEN_REQUEST:
            return Object.assign({}, state);
        case AUTH_WITH_TOKEN_SUCCESS:
            return Object.assign({}, state, action.payload);
        case AUTH_WITH_TOKEN_FAILED:
            return Object.assign({}, state, action.payload);
        case REFRESH_TOKEN_REQUEST:
            return Object.assign({}, state);
        case REFRESH_TOKEN_SUCCESS:
            return Object.assign({}, state, action.payload);
        case REFRESH_TOKEN_FAILED:
            return initialState;
        case REFRESH_TOKEN_NO_NEED:
            return state;
        default:
            return state;
    }
}