import React from 'react';
import {connect} from 'react-redux';
import NavBar from "../views/components/NavBar/NavBar";
import Header from "../views/components/Header/Header";
import Footer from "../views/components/Footer/Footer";
import ControlPanel from "../views/components/Users/ControlPanel";
import PropTypes from 'prop-types';
import {changeUser, getUsers} from "../actions/usersActions";
import User from "../views/components/Users/User";


class UsersList extends React.Component {
    constructor(props) {
        super(props);
        this.props.getUsers(this.props.user.accessToken, this.props.user.refreshToken, this.props.user.accessExpiresIn);
    }

    render() {
        const {users} = this.props.users;
        return (
            <div>
                <Header/>
                <NavBar/>
                {
                    (users.length > 0) ?
                        <div className="users-body">
                            <ControlPanel user={users[0]}/>
                            <div className="users__list-header">
                                {
                                    Object.keys(users[0]).map(e => {
                                        return <p className="users__list-header--title" key={e}>{e}</p>
                                    })
                                }
                            </div>
                            {
                                users.map(e => {
                                    return <User user={e} key={e.id} select={this.props.selectUser}/>
                                })
                            }
                        </div> :
                        <div style={{height: '100%', width: '100%'}}>false</div>
                }

                <Footer/>
            </div>
        );
    }
}

UsersList.propTypes = {
    user: PropTypes.object.isRequired,
    users: PropTypes.object.isRequired
};

const mapStateToProps = store => {
    return {
        user: store.authorization,
        users: store.users
    }
};
const mapDispatchToProps = dispatch => {
    return {
        getUsers: (accessToken, refreshToken, accessExpiresIn) => dispatch(getUsers(accessToken, refreshToken, accessExpiresIn)),
        selectUser: id => dispatch(changeUser(id)),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(UsersList);