import React from 'react';
import {connect} from 'react-redux';
import {signOut} from "../actions/authorizationActions";
import NavBar from "../views/components/NavBar/NavBar";
import Header from "../views/components/Header/Header";
import Footer from "../views/components/Footer/Footer";


class Authenticated extends React.Component {

    constructor(props){
        super(props);
        this.check=this.check.bind(this);
        this.interval=setInterval(this.check,1000);
    }

    check(){
        let time=new Date().getTime()/1000;
        let tokenTime=this.props.store.authorization.accessExpiresIn;
        if(time>tokenTime)
        {
            alert('Token was broken');
            alert(`Now time: ${new Date(time).toLocaleTimeString()}\nToken time: ${new Date(tokenTime).toLocaleTimeString()}`);
            clearInterval(this.interval);
        }
    }



    render() {
        const {store} = this.props;
        return (
            <div>
                <Header/>
                <NavBar/>
                <div className="authenticated-body">
                    <div className="authenticated--title">
                        <h3>Authenticated page</h3>
                    </div>
                    <ul className={"authenticated--content"}>
                        {
                            Object.keys(store.authorization).map(e => {
                                if (e !== 'token')
                                    return <li key={e}>{e}: {store.authorization[e]}</li>;
                                else
                                    return false;
                            })
                        }
                    </ul>
                    <button className={"authenticated--button"} onClick={this.props.signOut}>Log out</button>
                </div>
                <Footer/>
            </div>
        );
    }
}


const mapStateToProps = store => {
    return {
        store: store
    }
};
const mapDispatchToProps = dispatch => {
    return {
        signOut: () => dispatch(signOut())
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Authenticated);
