import {
    DELETE_USER_FAILED,
    DELETE_USER_REQUEST, DELETE_USER_SUCCESS, INSERT_USER_FAILED,
    INSERT_USER_REQUEST, INSERT_USER_SUCCESS,
    GET_USERS_FAILED,
    GET_USERS_REQUEST, GET_USERS_SUCCESS, UPDATE_USER_FAILED,
    UPDATE_USER_REQUEST, UPDATE_USER_SUCCESS, CHANGE_USER
} from "../constants/users";
import axios from "axios";
import {refreshAccessToken} from "./authorizationActions";

export function getUsers(accessToken,refreshToken,accessExpiresIn) {
    return dispatch => {
        dispatch({
            type: GET_USERS_REQUEST
        });
        console.log('\n\n\n\n\n');
        dispatch(refreshAccessToken(refreshToken,accessExpiresIn));
        console.log('\n\n\n\n\n');
        // setTimeout(()=>{
        //     dispatch({
        //         type:GET_USERS_SUCCESS,
        //         payload:[
        //             {
        //             id:1,
        //                 login:"userlogin",
        //                 password:"password",
        //                 firstName:"name",
        //                 lastName:"lastname",
        //                 email:"email"
        //         },{
        //                 id:2,
        //                 login:"userlogin",
        //                 password:"password",
        //                 firstName:"name",
        //                 lastName:"lastname",
        //                 email:"email"
        //             },{
        //                 id:3,
        //                 login:"userlogin",
        //                 password:"password",
        //                 firstName:"name",
        //                 lastName:"lastname",
        //                 email:"email"
        //             }
        //         ]
        //     })
        // },5000);

        axios.post('/request/users/get', {token: accessToken})
            .then(response => {
                if (response.data.users)
                    dispatch({
                        type: GET_USERS_SUCCESS,
                        payload: response.data.users
                    });
                else
                    throw new Error('Empty result');
            })
            .catch(reason => {
                if(reason.response && reason.response.status ===401) {
                    alert('TOKEN BROKEN');
                }
                else
                    dispatch(
                    {
                        type: GET_USERS_FAILED,
                        payload: reason
                    }
                )
            });
    }
}

export function changeUser(id) {
    return ({
        type: CHANGE_USER,
        payload: id
    });
}

export function insertUser(data) {
    return dispatch => {
        dispatch({
            type: INSERT_USER_REQUEST
        });


        axios.post('/request/users/insert', {user: data.user, token: data.token})
            .then(response => {
                if (response.data.users)
                    dispatch({
                        type: INSERT_USER_SUCCESS,
                        payload: response.data.users
                    });
                else
                    throw new Error('Failed insert');
            })
            .catch(reason => {
                dispatch({
                    type: INSERT_USER_FAILED,
                    payload: reason
                })
            });
    }
}

export function updateUser(data) {
    return dispatch => {
        dispatch({
            type: UPDATE_USER_REQUEST
        });

        axios.put('/request/users/update', {userId: data.id, token: data.token})
            .then(response => {
                if (response.data.users)
                    dispatch({
                        type: UPDATE_USER_SUCCESS,
                        payload: response.data.users
                    });
                else
                    throw new Error('Failed update');
            })
            .catch(reason => {
                dispatch({
                    type: UPDATE_USER_FAILED,
                    payload: reason
                })
            });
    }
}

export function deleteUser(data) {
    return dispatch => {
        dispatch({
            type: DELETE_USER_REQUEST,
            payload: data.id
        });


        axios.post('/request/users/delete', {userId: data.id, token: data.token})
            .then(response => {
                if (response.data.users)
                    dispatch({
                        type: DELETE_USER_SUCCESS,
                        payload: response.data.users
                    });
                else
                    throw new Error('Failed delete');
            })
            .catch(reason => {
                dispatch({
                    type: DELETE_USER_FAILED,
                    payload: reason
                })
            });
    }
}