import React from 'react';
import {connect} from 'react-redux';
import {BrowserRouter} from "react-router-dom";
import Routes from "../routes";
import PropTypes from "prop-types";
import {restoreToken, signInWithToken} from "../actions/authorizationActions";

class App extends React.Component {
    constructor(props) {
        super(props);
        const {authorization}=this.props;
        if(authorization.accessToken===null && authorization.refreshToken==null && !authorization.accessExpiresIn) {
            if(authorization.accessToken!==null && authorization.login==="" && authorization.id===null)
                this.props.bearerToken(authorization.accessToken);
        }
    }

    render() {
        return (
            <BrowserRouter>
                <Routes/>
            </BrowserRouter>
        );
    }
}

App.propsTypes = {
    authorization: PropTypes.object.isRequired
};
const mapStateToProps = store => {
    return {
        authorization:store.authorization
    }
};
const mapDispatchToProps = dispatch => {
    return {
        bearerToken:token=>dispatch(signInWithToken(token)),
        restoreToken:()=>dispatch(restoreToken())
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(App);