import React from 'react';
import {NavLink} from "react-router-dom";

class NavBarLink extends React.Component {


    render() {
        return (
            <NavLink
                exact={this.props.exact}
                className={"nav__link"}
                activeClassName={"nav__link__active"}
                to={this.props.link}
            >
                {this.props.name}
            </NavLink>
        );
    }
}


export default NavBarLink;